<template>
  <div class="layOutBor">
    <el-container class="container">
      <el-header class="header">
        <common-headbar :propsname='userName' :isTodo="isTodo" :isNews="isNews"></common-headbar>
      </el-header>
      <el-container>
        <el-aside width="210px" class="side">
          <left-menu :menuList="menuData" :currActive="defaultActive"></left-menu>
        </el-aside>
        <el-main class="content">
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script type="text/javascript">
import CommonHeadbar from "../components/headBar";
import LeftMenu from "../components/leftMenu";
import { getLeftMenuList } from "@/API/getleftmenu";

// 
const menuList = [
  {
    name: "homepage",
    label: "",
    meta: { icon: "icon-renminchubanshe" },
    checked: true // 
  },
  {
    name: "2",
    label: "",
    meta: { icon: "icon-dangzhuanhuan" },
    children: [
      {
        name: "PartyUser",
        label: ""
      },
      {
        name: "Empower",
        label: ""
      }
    ]
  },
  {
    name: "map",
    label: "",
    meta: { icon: "icon-yingshezhuanhuan" }
  },
  {
    name: "3",
    label: "",
    meta: { icon: "icon-ziyuanguanli" },
    children: [
      {
        name: "Article",
        label: ""
      },
      {
        name: "Video",
        label: ""
      }
    ]
  },
  {
    name: "4",
    label: "",
    meta: { icon: "icon-lanmu" },
    children: [
      {
        name: "ArticleSection",
        label: ""
      },
      {
        name: "VideoSection",
        label: ""
      }
    ]
  },
  {
    name: "infor",
    label: "",
    meta: { icon: "icon-xinxijianguanzhuanhuan" }
  },
  {
    name: "chart",
    label: "",
    meta: { icon: "icon-shujufenxizhuanhuan" }
  },
  {
    name: "wechat",
    label: "",
    meta: { icon: "icon-gongzhonghao" }
  },
  {
    name: "5",
    label: "",
    meta: { icon: "icon-xitongguanli" },
    children: [
      {
        name: "Publisher",
        label: ""
      },
      {
        name: "Role",
        label: ""
      }
    ]
  }
];
export default {
  name: "layOut",
  data() {
    return {
      userID: "",
      menuData: [],
      defaultActive: '', //  index
      isTodo: false, // 
      isNews: false // 
    };
  },
  components: {
    CommonHeadbar,
    LeftMenu
  },
  watch: {
    $route(to) {
      this.defaultActive = this.$route.name
      localStorage.setItem('defaultActive', this.$route.name)
    }
  },
  created() {
    this.search();
    this.getmenuData();
  },
  methods: {
    search() {
      // this.userID = localStorage.getItem("userId");
      this.userName = localStorage.getItem("userName");
    },
    getmenuData() {
      // 
      getLeftMenuList({
        userId: localStorage.getItem("userId"),
        userType: "1"
      })
        .then(res => {
          if (res.result && res.result.length) {
            var data = res.result;
            this.dealData(data);
          } else {
            this.dealData();
          }
        })
        .catch(() => {
          // 
          this.menuData = menuList
        });
    },
    dealData(data) {
      var arr2 = [];
      if (data && data.length) {
        // 2. data
        data.forEach(item => {
          // ''''
          if(item.label == ''){
            this.isTodo = true
          }
          if(item.label == ''){
            this.isNews = true
          }
          if (item.children && item.children.length) {
            var obj = {
              label: item.label
            };
            arr2.push(obj);
            item.children.forEach(foo => {
              arr2.push(foo);
            });
          } else {
            arr2.push(item);
          }
        });

        // 3. checked:true
        for (var i = 0; i < menuList.length; i++) {
          var _obj = menuList[i];
          var _obj2 = menuList[i].children;

          // 
          for (var j = 0; j < arr2.length; j++) {
            if (arr2[j].label === _obj.label) {
              _obj.checked = true;
            }
          }

          // children
          if (_obj2 && _obj2.length) {
            for (var k = 0; k < _obj2.length; k++) {
              var _obj3 = _obj2[k];
              for (var m = 0; m < arr2.length; m++) {
                if (arr2[m].label === _obj3.label) {
                  _obj3.checked = true;
                }
              }
            }
          }
        }
      }

      this.menuData = menuList
    }
  }
};
</script>

<style lang="less" scoped>
@import "../style/mix";
.layOutBor {
  .wh(100%, 100%);
  background-color: #f5f5f5;
  .container {
    .wh(100%, 100%);
    min-width: 1200px;
    padding: 0;
    .header {
      .wh(100%, 113px) !important;
      min-width: 1200px;
      padding: 0;
    }
    .side {
      width: 210px;
      overflow-y: scroll;
      overflow-x: hidden;
      padding: 0;
    }
    .content {
      width: calc(100% - 210px);
      min-width:965px;
      margin-left: 210px;
      background-color: #f5f5f5;
      overflow-y: scroll;
      overflow-x: hidden;
      position: fixed;
      top: 113px;
      bottom: 0px;
    }
  }
}
</style>




