import { Validator } from 'vee-validate'
import CN from 'vee-validate/dist/locale/zh_CN'

// VeeValidate,
Validator.localize(CN)

// 
const dictionary = {
  CN: {
    messages: {
      password: function (field) {
        return field + '8-16'
      },
      user: function (field) {
        return  '4'
      },
      telephone: function (field) {
        return  ''
      },
      confirmPassword: function () {
        return ''
      },
      email: function () {
        return ''
      },
      url: function () {
        return ''
      },
      menuName: function (field) {
        return '4'
      },
      menuName2: function (field) {
        return '7'
      },
      required: function (field) {
        return '' + field
      }
    },
    attributes: {
      userEmail: '',
      password: '',
      userId: '',
      telephone: '',
      oldPassword: '',
      newPassword: '',
      surePassword: '',
      partyName:'',
      partyName1:'',
      party:'',
      typeSelected:"",
      account:"",
      initPwd:"",
      value:"",
      times:"",
      linkPhone:"",
      area:"",
      role:"",
      selectedType:"",
      artTitle: '',
      authorName: '',
      artTime: '',
      artContent: '',
      vidName: '',
      videoUrl: '',
      vidTime: '',
      vidContent: '',
      relUrl: '',
      mapUrl: '',
      mapName: '',
      sectionName: '',
      msgName: '',
      menuNameA: '',
      menuTypeA: '',
      menuUrlA: '',
      rolename:""
    }
  }
}

Validator.localize(dictionary)

// 
Validator.extend('telephone', {
  getMessage: field => field + '11',
  validate: value => {
    return value.length === 11 && /^1[34578]\d{9}$/.test(value)
  }
})

// 
Validator.extend('userName', {
  getMessage: field => field + '',
  validate: value => {
    return /^[\u4e00-\u9fa5]+$/.test(value)
  }
})

// 
Validator.extend('password', {
  getMessage: field => field + '8-16',
  validate: value => {
    return /^(?![0-9]*$)[a-zA-Z0-9_]{8,16}$/.test(value)
  }
})
//
Validator.extend('user', {
  getMessage: field => field + '',
  validate: value => {
    return /^(?![0-9]*$)[a-zA-Z0-9_]{4,16}$/.test(value)
  }
})
// /
Validator.extend('userEmail', {
  getMessage: field => '',
  validate: value => {
    return /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/.test(value)
  }
})

// 

// 
Validator.extend('confirmPassword', {
  getMessage: field => '',
  validate: (value, val2) => {
    return value === val2[0]
  }
})

// 
Validator.extend('menuName', {
  getMessage: field => '4',
  validate: value => {
    return /^[\u4e00-\u9fa5]{1,4}$/.test(value)
  }
})
Validator.extend('menuName2', {
  getMessage: field => '7',
  validate: value => {
    return /^[\u4e00-\u9fa5]{1,7}$/.test(value)
  }
})

export default Validator



