import axios from "axios";
import { Message, MessageBox } from "element-ui";
import $AjaxUrl from './severurl'

// axios
const service = axios.create({
  baseURL: process.env.NODE_ENV == 'production' ?
  'http://tc.ccpph.com.cn/Psms' : $AjaxUrl,
  // baseURL:  'http://10.0.60.101:8080/Psms', //  'http://10.0.18.111:8080/Psms', // 'http://10.0.18.111:8080/Psms', //  "http://172.16.0.15:8080/Psms", //  'http://10.0.18.111:8080/psms', //  // process.env.BASE_API, // apibase_url
  timeout: 15000 // 
});

// request
service.interceptors.request.use(
  config => {
    if(sessionStorage.getItem("token")) {
      config.headers["token"] = sessionStorage.getItem("token");
    }
    return config;
  },
  error => {
    // Do something with request error
    console.log(error); // for debug
    return Promise.reject(error);
  }
);

// respone
service.interceptors.response.use(
  response => {
    /**
     * code200 
     */

    const res = response.data;
    if (res.code == 200) {
      return response.data;
    } else if (res.code == 900) {
      // Message({
      //   message: res.massege || "",
      //   type: "error",
      //   duration: 5 * 1000
      // });
      return response.data;
    } else if (!res.code) {
      return response.data;
    } else {
      var errorMsg = "~~~";

      if (res.code === 500) {
        errorMsg = "";
      }

      if (res.code === 10001) {
        errorMsg = "  ";
      }

      if (res.code === 10002) {
        errorMsg = "  ";
      }

      Message({
        message: errorMsg,
        type: "error",
        duration: 5 * 1000
      });

      if (res.code === 10003) {
        // token;  Token 
        MessageBox.confirm(
          "",
          "",
          {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          }
        ).then(() => {
          sessionStorage.removeItem("token");
          location.reload();
        });
      }

      // if (res.code === 401) {
      //   console.log(":" + res.msg);
      //   errorMsg = "";
      // }
      return Promise.reject("error");
    }
  },
  error => {
    console.log("err" + error); // for debug
    return Promise.reject("");
  }
);

export default service;



