import request from './request'

/**
 * [getTodoList ]
 **/
export function getTodoList(arg) {
  return request({
    url: `/findAudit`,
    method: 'post',
    data: arg,
    // withCredentials: true
  })
}

/**
 * [agreeTodo ]
 **/
export function agreeTodo(arg) {
  return request({
    url: `/updateAudit`,
    method: 'post',
    data: arg,
    // withCredentials: true
  })
}

/**
 * [findTodoArticle ]
 **/
export function findTodoArticle(arg) {
  return request({
    url: `/findArticle`,
    method: 'post',
    data: arg,
    // withCredentials: true
  })
}

/**
 * [findTodoVideo ]
 **/
export function findTodoVideo(arg) {
  return request({
    url: `/findVideo`,
    method: 'post',
    data: arg,
    // withCredentials: true
  })
}

/**
 * [getRemind ]
 **/
export function getRemind(arg) {
  return request({
    url: `/findAuditCount`,
    method: 'post',
    data: arg,
    // withCredentials: true
  })
}


const API = {
  getTodoList,
  agreeTodo,
  findTodoArticle,
  findTodoVideo,
  getRemind
}
export default API



